import React, { useState, useEffect, Fragment } from 'react'

import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';

import Tooltip from '@material-ui/core/Tooltip'

import MaintenanceDetail from './MaintenanceDetail';
import MaintenanceForm from './MaintenanceForm';
import { Button } from '@material-ui/core';

import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import Slide from '@material-ui/core/Slide';

import TablePagination from '@material-ui/core/TablePagination';

import { withStyles } from '@material-ui/core/styles';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography';
import { listMaintenances, deleteMaintenance, onSaveObservations, onSendReportMaintenance, payMaintenance } from '../../api/api';
import Chip from '@material-ui/core/Chip';
import AddIcon from '@material-ui/icons/Add';
import { AiOutlineComment } from 'react-icons/ai';
import { FiSend, FiSearch } from 'react-icons/fi';

import CircularProgress from '@material-ui/core/CircularProgress';

import Fab from '@material-ui/core/Fab';

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Swal from 'sweetalert2'
import TableContainer from '@material-ui/core/TableContainer';
import { AiOutlineCloudDownload } from "react-icons/ai";
import { MdPaid } from "react-icons/md";
import config from '../../config';


const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1,
    },
    iconWidth: {
        width: 10
    },
    paper: {
        padding: theme.spacing(2),
        marginTop: 10,
    },
    button: {
        margin: theme.spacing(1),
    },
    input: {
        display: 'none',
    },
    fab: {
        margin: theme.spacing(1),
    },
    extendedIcon: {
        marginRight: theme.spacing(1),
    },
    icon: {
        marginRight: theme.spacing(1),
        color: 'black'
    },
}))

const styles = (theme) => ({
    root: {
        margin: 0,
        padding: theme.spacing(2),
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    }
});

const DialogTitle = withStyles(styles)((props) => {
    const { children, classes, onClose, ...other } = props;
    return (
        <MuiDialogTitle disableTypography className={classes.root} {...other}>
            <Typography variant="h6">{children}</Typography>
            {onClose ? (
                <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
                    <CloseIcon />
                </IconButton>
            ) : null}
        </MuiDialogTitle>
    );
});

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
})

const Maintenances = props => {

    useEffect(() => {
        console.log(props)
        let downloaded = false
        if (props.location.pathname === "/maintenances-downloaded") {
            downloaded = true
            setDownloaded(true)
        }
        let data = { start: 0, end: rowsPerPage, paginate: true, "source": "web", downloaded }

        listMaintenances(data).then(response => {
            setMaintenances(response.maintenances);
            setTotalMaintenances(response.count);
        });
    }, []);

    const classes = useStyles()

    const [downloaded, setDownloaded] = useState(false)
    const [maintenances, setMaintenances] = useState([])
    const [maintenance, setMaintenance] = useState(null)
    const [totalMaintenances, setTotalMaintenances] = useState(0)
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(10);


    const [open, setOpen] = useState(false);
    const [openDetailMaintenance, setOpenDetailMaintenance] = useState(false);
    const [openMaintenanceForm, setOpenMaintenanceForm] = useState(false);

    const [openObservations, setOpenObservations] = useState(false);
    const [observations, setObservations] = useState('');
    const [loading, setLoading] = useState(false);
    const [loadingReport, setLoadingReport] = useState(false);

    const [pdf, setPdf] = useState(false)
    const [dataPdf, setDataPdf] = useState('')


    const handleChangePage = (_, newPage) => {
        setPage(newPage);
        let data = { start: (newPage * rowsPerPage), end: rowsPerPage, paginate: true, downloaded }
        listMaintenances(data).then(response => {
            setMaintenances(response.maintenances);
            setTotalMaintenances(response.count);
        });
    }

    const handleChangeRowsPerPage = event => {

        setRowsPerPage(+event.target.value);
        setPage(0);
        let data = { start: 0, end: event.target.value, downloaded }
        listMaintenances(data).then(response => {
            setMaintenances(response.maintenances);
            setTotalMaintenances(response.count);
        });
    }

    const onCloseDeleteMaintenance = () => {
        setOpen(false);
    }

    const onRefreshMaintenances = () => {
        listMaintenances().then(response => {
            setMaintenances(response.maintenances);
            setTotalMaintenances(response.count);
            setRowsPerPage(1)
        });
    }

    const onDeleteMaintenance = id => {
        setOpen(false);

        deleteMaintenance(id).then(response => {
            if (response.status === 'success') {
                toast.success(response.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
                const newMaintenances = maintenances.map((currentMaintenance) => {
                    if (id !== currentMaintenance._id)
                        return currentMaintenance

                    currentMaintenance.status = "deleted";
                    return currentMaintenance
                })
                setMaintenances(newMaintenances)
            } else {
                toast.error(response.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
            }
        }).catch(e => {

            toast.error('Ocurrió un error al eliminar el proyecto', {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        })

    }

    const onDetailMaintenance = maintenance => () => {
        setMaintenance(maintenance)
        setOpenDetailMaintenance(true)
    }

    const onOpenObservation = maintenance => () => {
        setOpenObservations(true);
        setMaintenance(maintenance);
    }

    const onPayMaintenance = maintenance => () => {
        if (maintenance.statusPayment == 'pendingPayment') {
            const newMaintenances = maintenances.map(currentMaintenance => {
                if (currentMaintenance._id === maintenance._id) {
                    currentMaintenance.paying = true;
                }
                return currentMaintenance;
            });
            setMaintenances(newMaintenances)


            payMaintenance({ id: maintenance._id }).then(response => {
                const newMaintenances = maintenances.map(currentMaintenance => {
                    if (currentMaintenance._id === maintenance._id) {
                        currentMaintenance.paying = false;
                    }
                    return currentMaintenance;
                });
                setMaintenances(newMaintenances)

                if (response.status === 'success') {
                    const newMaintenances = maintenances.map(currentMaintenance => {
                        if (currentMaintenance._id === maintenance._id) {
                            currentMaintenance.statusPayment = 'paid';
                        }
                        return currentMaintenance;
                    });
                    setMaintenances(newMaintenances)


                    Swal.fire({
                        icon: 'success',
                        title: response.message,
                        showConfirmButton: false,
                        timer: 1500
                    });
                }
            });
        } else {
            Swal.fire({
                icon: 'warning',
                title: `El mantenimiento ya se encuentra pagado`,
                showConfirmButton: false,
                timer: 1500
            });
        }
    }

    const closeDatailMaintenance = () => {
        setOpenDetailMaintenance(false)
    }

    const onOpenMaintenanceForm = () => {
        setOpenMaintenanceForm(true)
    }

    const onCloseMaintenanceForm = () => {
        setOpenMaintenanceForm(false)
    }

    const onCloseObservations = () => {
        setOpenObservations(false);
    }

    const saveObservations = () => {
        console.log(observations);
        console.log(maintenance);
        setLoading(true);
        let data = {
            observations,
            id: maintenance._id
        }
        onSaveObservations(data).then(response => {
            setLoading(false);
            setOpenObservations(false);

            if (response.status === 'success') {
                Swal.fire({
                    icon: 'success',
                    title: 'Mensaje',
                    text: response.message,
                    showConfirmButton: false,
                    timer: 3000
                })
            } else {
                Swal.fire({
                    icon: 'error',
                    title: 'Error',
                    text: response.message,
                    showConfirmButton: false,
                    timer: 3000
                })
            }

        }).catch(e => {
            console.log(e);
            setLoading(false);

        })

    }

    const onClosePdf = () => {
        setPdf(false)
    }

    const sendReportMaintenance = (id, type) => () => {

        const newMaintenances = maintenances.map(currentMaintenance => {
            if (currentMaintenance._id === id) {
                if (type == "download")
                    currentMaintenance.downloading = true;
                else
                    currentMaintenance.loading = true;
            }
            return currentMaintenance;
        });

        setMaintenances(newMaintenances);

        onSendReportMaintenance({ id, type }).then(response => {
            const newMaintenances = maintenances.map(currentMaintenance => {
                if (currentMaintenance._id === id) {
                    if (type == "download")
                        currentMaintenance.downloading = false;
                    else
                        currentMaintenance.loading = false;
                }
                return currentMaintenance;
            });


            setMaintenances(newMaintenances);

            if (response.status === 'success') {
                if (type != "download") {
                    Swal.fire({
                        icon: 'success',
                        title: 'Mensaje',
                        text: response.message,
                        showConfirmButton: false,
                        timer: 3000
                    })
                } else {
                    const newMaintenances = maintenances.filter(currentMaintenance => currentMaintenance._id != id)
                    setMaintenances(newMaintenances);

                    window.open(`${config.urlPdf}${id}.pdf`)
                }

            } else {
                console.log(response)
                Swal.fire({
                    icon: 'error',
                    title: 'Error',
                    text: response.message,
                    showConfirmButton: false,
                    timer: 3000
                })
            }

        }).catch(e => {
            console.log(e);
            setLoading(false);

        })

    }

    const onChangeDescription = e => {
        setObservations(e.target.value);

    }



    return (
        <Fragment>
            <ToastContainer />
            <Paper className={classes.paper}>
                <Fab onClick={onOpenMaintenanceForm} variant="extended" color="primary" size="small" className={classes.margin}>
                    <AddIcon className={classes.extendedIcon} />
                    Nuevo mantenimiento
                </Fab>

                <TableContainer component={Paper}>
                    <Table>
                        <TableHead>
                            <TableRow>
                                <TableCell>DESCRIPCIÓN</TableCell>
                                <TableCell>CLIENTE</TableCell>
                                <TableCell>TABLEROS</TableCell>
                                <TableCell>DETALLE</TableCell>
                                <TableCell>ENVIAR INFORME</TableCell>
                                <TableCell>DESCARGAR INFORME</TableCell>
                                <TableCell>OBSERVACIONES</TableCell>
                                <TableCell>PAGADO</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {
                                maintenances.map(maintenance => {
                                    return (
                                        <TableRow key={maintenance._id} className={maintenance.statusPayment}>
                                            <TableCell>{maintenance.name} </TableCell>
                                            <TableCell>{maintenance.customer.name}</TableCell>
                                            <TableCell className={classes.iconWidth}>
                                                <Chip size='small' label={maintenance.boards.length} variant="outlined" />
                                            </TableCell>
                                            <TableCell className={classes.iconWidth}>
                                                <FiSearch className={'icon'} onClick={onDetailMaintenance(maintenance)} />
                                            </TableCell>
                                            <TableCell className={classes.iconWidth}>
                                                {
                                                    maintenance.loading ?
                                                        <div className='circularProgress'>
                                                            <CircularProgress size={16} />
                                                        </div> :
                                                        <FiSend className={'icon'} onClick={sendReportMaintenance(maintenance._id, "email")} />
                                                }
                                            </TableCell>
                                            <TableCell className={classes.iconWidth}>
                                                <Tooltip title="Descargar" placement="top">
                                                    {
                                                        maintenance.downloading ?
                                                            <div className='circularProgress'>
                                                                <CircularProgress size={16} />
                                                            </div> :
                                                            <div>
                                                                <AiOutlineCloudDownload className={"icon"} onClick={sendReportMaintenance(maintenance._id, "download")} />
                                                            </div>
                                                    }


                                                </Tooltip>
                                            </TableCell>

                                            <TableCell className={classes.iconWidth}>
                                                <AiOutlineComment className={'icon'} onClick={onOpenObservation(maintenance)} />
                                            </TableCell>
                                            {
                                                maintenance.statusPayment ?
                                                    <TableCell className={classes.iconWidth}>
                                                        {
                                                            maintenance.paying ?
                                                                <div className='circularProgress'>
                                                                    <CircularProgress size={16} />
                                                                </div> :
                                                                <div>
                                                                    <MdPaid className={`icon ${maintenance.statusPayment === 'paid' ? 'paid' : ''}`} onClick={onPayMaintenance(maintenance)} />
                                                                </div>
                                                        }
                                                    </TableCell>
                                                    : <TableCell></TableCell>

                                            }

                                        </TableRow>
                                    )
                                })
                            }
                        </TableBody>
                    </Table>

                </TableContainer>
                <TablePagination
                    rowsPerPageOptions={[10, 20, 100]}
                    component="div"
                    count={totalMaintenances}
                    rowsPerPage={rowsPerPage}
                    page={page}
                    onChangePage={handleChangePage}
                    onChangeRowsPerPage={handleChangeRowsPerPage}
                />
            </Paper>

            <Dialog
                maxWidth="lg"
                open={pdf}
                onClose={onClosePdf}
                aria-labelledby="draggable-dialog-title">
                <DialogTitle id="draggable-dialog-title">Reporte de servicios</DialogTitle>
                <DialogContent>
                    <iframe src={dataPdf} title="Imprimir" height="900px" width="600px" ></iframe>
                </DialogContent>
                <DialogActions>

                    <Button onClick={onClosePdf} color="primary">
                        Cerrar
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog
                open={open}
                onClose={onCloseDeleteMaintenance}>
                <DialogTitle >
                    Alerta
                </DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Esta seguro de eliminar esta atención?
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={onCloseDeleteMaintenance} color="primary">
                        NO
                    </Button>
                    <Button onClick={onDeleteMaintenance} color="primary">
                        SI
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog maxWidth="md" open={openMaintenanceForm} onClose={onCloseMaintenanceForm} TransitionComponent={Transition}>
                <DialogContent>
                    <MaintenanceForm onRefreshMaintenances={onRefreshMaintenances} onClose={onCloseMaintenanceForm} maintenance={maintenance} />
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={onCloseMaintenanceForm} color="primary">
                        Cerrar
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog
                fullWidth
                fullScreen={true}
                open={openDetailMaintenance}
                onClose={closeDatailMaintenance}
                TransitionComponent={Transition}>
                <DialogContent>
                    <MaintenanceDetail maintenance={maintenance} />
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={closeDatailMaintenance} color="primary">
                        Cerrar
                    </Button>
                </DialogActions>
            </Dialog>



            <Dialog
                fullWidth={true}
                maxWidth={'sm'}
                open={openObservations}
                onClose={onCloseObservations}>
                <DialogTitle>
                    Observaciones
                </DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        <TextField
                            autoComplete='off'
                            multiline
                            autoFocus
                            margin="dense"
                            id="name"
                            label="Observaciones"
                            onChange={onChangeDescription}
                            fullWidth
                        />
                    </DialogContentText>
                </DialogContent>
                <DialogActions>

                    {
                        loading ?
                            <div className='circularProgress'>
                                <CircularProgress />
                            </div> :

                            <Fragment>
                                <Button autoFocus onClick={onCloseObservations} color="primary">
                                    Cancelar
                                </Button>
                                <Button onClick={saveObservations} color="primary">
                                    Guardar
                                </Button>
                            </Fragment>
                    }
                </DialogActions>
            </Dialog>

        </Fragment>)
}

export default Maintenances